.about{
  padding: 100px 0;
  position: relative;
  z-index: 1;
  &::before{
    background-image: url(../img/hearth.png);
    background-repeat: no-repeat;
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    background-size: 85%;
    background-position: 0 100%;
  }
  & > h2{
    font-size: 30px;
    font-weight: bold;
    letter-spacing: 1.16px;
    color: $grafit-color;
    text-align: center;
    margin-bottom: 55px;
  }
  &-card{
    border-radius: 8px;
    background-color: $white-color;
    box-shadow: -2px -1px 9px 0 rgba(0,0,0,0.17);
    padding: 100px 75px 130px;
    width: 918px;
    margin: 0 auto;
    text-align: center;
    & > p{
      font-size: 15px;
      letter-spacing: 1.34px;
      color: $grafit-color;
    }
  }
}