.message {
  position: relative;
}

.message-check input[type=radio].css-font,
.message-check input[type=checkbox].css-font {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  cursor: pointer;
}

.message-check input[type=radio].css-font ~ label:before,
.message-check input[type=checkbox].css-font ~ label:before {
  font-family: FontAwesome;
  display: inline-block;
  content: "0";
  letter-spacing: 10px;
  color: #aaa;
  width: 1.4em;
  cursor: pointer;
}

.message-check input[type=checkbox].css-font ~ label:before {
  content: "\f096";
  cursor: pointer;
}

.message-check input[type=checkbox].css-font:checked ~ label:before {
  content: "\f046";
  color: #037286;
  cursor: pointer;
  display: block;
}

.message-check > label {
  display: flex;
  line-height: 1;
  font-size: 10px;
  letter-spacing: 0.56px;
  margin-bottom: 0;
}

.message-check::before {
  content: ' ';
  position: absolute;
  left: 10px;
  top: -10px;
  border-left: 10px solid #fff;
  border-top: 10px solid transparent;
  border-bottom: 1px solid #fff;
  -webkit-filter: drop-shadow(-3.7px 4.7px 2.7px rgba(0, 0, 0, 0.15));
  filter: drop-shadow(0px -1px 1px rgba(0, 0, 0, 0.15));
}

.message > a > i {
  color: #848484 !important;
  font-size: 20px;
}

.message-item {
  box-shadow: -1px 0 5px 0 rgba(129, 127, 127, 0.5);
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  position: absolute;
  left: 15px;
  z-index: 100;
}

.message-item span {
  font-size: 10px;
  letter-spacing: 0.56px;
  color: #3F4C55;
  display: block;
  text-align: center;
  margin-bottom: 18px;
}

.message-wrapp {
  display: flex;
  justify-content: space-between;
}

.message-wrapp > a {
  background: #037286;
  border-radius: 4px;
  width: 75px;
  font-size: 10px;
  font-weight: bold;
  letter-spacing: 0.56px;
  color: #fff;
