  align-items: center;
  justify-content: center;
  margin-right: 18px;
}

.message-new {
  position: absolute;
  left: 10px;
  top: -5px;
  height: 25px;
  width: 25px;
  background-color: #DB0101;
  border-radius: 100%;
}

.message-new span {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
  color: #fff;
  width: 100%;
  height: 100%;
}

input[type="radio"]:before {
  content: " ";
  display: block;
  background-color: white;
  border: 1px solid #D8D8D8;
  width: 16px;
  height: 16px;
  margin-right: 10px;
  border-radius: 100px;
  visibility: initial;
}

input[type="radio"]:after {
  content: " ";
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 100px;
  position: absolute;
  top: 4px;
  left: 4px;
  visibility: initial;
  background-color: #ffffff;
  transition: background-color 0.2s ease-in-out;
}

input[type="radio"]:checked:after {
  background: #368CCA;
}

